onMounted(() => {
    viewer = new Viewer(document.querySelector("#main") as HTMLElement);
    messageDom = document.querySelector("#messages");
    themeUtil.load();
    // MathJax.initMathjaxConfig();
});

/**
 * 确认 key
 */
const confirmKey = ref("");
function okKey() {
    if (confirmKey.value == "") {
        return messageUtil({
            type: "warning",
            content: "key 不能为空"
        });
    }

    config.key = confirmKey.value;
    save();

    okKeyDialog.value = false;
    messageUtil({
        type: "success",
        content: "key 存储成功，开始提问吧"
    });
}

// 是否正在加载
const loading = ref(false);

/**
 * 提交问题
 */
async function submit() {
    loading.value = true;
    await axios({
        method: "post",
        url: "https://node.fatshady.cn/cors",
        timeout: 600000,
        data: {
            method: "POST",
            url: "https://api.openai.com/v1/chat/completions",
            data: {
                model: config.data.model,
                messages: clients[clientsIndex.value].contents
            },
            timeout: 60000,
            headers: {
                Authorization: `Bearer ${config.key}`
            }
        }
    })
        .then((res: any) => {
            if (res.data.status == 400) {
                throw res.data;
            }
            console.log(res);

            pushResult("assistant", res.data.data.choices[0].message.content);
        })
        .catch((err) => {
            console.error("请求报错了！", err);
            pushResult(
                "assistant",
                "",
                `网络请求错误，请联系站长排查！错误内容：
                    \`\`\`${JSON.stringify(err.msg)}\`\`\``
            );
            if (err.msg?.error?.code == "invalid_api_key") {
                messageUtil({
                    type: "danger",
                    content: `key 错误，请重新输入`
                });
                config.key = "";
                save();
                okKeyDialog.value = true;
            }
        })
        .finally(() => {
            loading.value = false;
        });
}

/**
 * 存入数据
 */
async function pushResult(
    role: "user" | "assistant" | "system",
    content: string,
    errContent?: string
) {
    clients[clientsIndex.value].contents.push({
        role,
        content: errContent ? errContent : content
    });
    hljsInit();
    scrollToBottom();
    saveMessage();
    console.log(
        "🚀 对话结果： | clients[clientsIndex.value].contents:",
        clients[clientsIndex.value].contents
    );
    await nextTick();
    viewer.update();
}

/**
 * 保存会话
 */
function saveMessage() {
    window.localStorage.setItem("message-data", JSON.stringify(clients));
}

// 消息框内容
const message = ref("");

/**
 * 发送消息
 */
function send() {
    if (message.value == "" || message.value.trim() == "" || loading.value) {
        return;
    }

    // 没有对话时
    if (clientsIndex.value == -1) {
        clients.unshift({
            name: "",
            contents: []
        });
        clientsIndex.value = 0;
    }

    // 是否设置了行为
    if (config.system && clients[clientsIndex.value].contents.length == 0) {
        pushResult("system", config.system);
    }

    pushResult("user", message.value);

    // 第一句对话修改标题
    if (
        clients[clientsIndex.value].contents.length == 1 ||
        (clients[clientsIndex.value].contents[0].role == "system" &&
            clients[clientsIndex.value].contents.length == 2)
    ) {
        const cacheName = message.value;
        clients[clientsIndex.value].name = cacheName;
        document.title = cacheName + " | ChatGPT";
    }

    // 清空输入框
    message.value = "";

    submit();
}

/**
 * 滚动到底部
 */
async function scrollToBottom() {
    await nextTick();
    messageDom = document.querySelector("#messages");
    if (messageDom) {
        const childrens = messageDom.children;
        let height = 0;
        if (childrens[childrens.length - 1]) {
            height += childrens[childrens.length - 1].clientHeight;
        }
        if (childrens[childrens.length - 2]) {
            height += childrens[childrens.length - 2].clientHeight;
        }
        messageDom.scrollTo({
            top: messageDom.scrollHeight - height
        });
    } else {
        scrollToBottom();
    }
}

// shift 是否按住
let shiftDown = false;

/**
 * 消息框键盘按下
 */
function keydown(event: KeyboardEvent) {
    // shift 是否按下
    if (event.keyCode === 16) {
        shiftDown = true;
    }
    // shift 不按住与 enter 按下，则发送
    if (!shiftDown && event.keyCode === 13) {
        send();
        event.preventDefault();
        return false;
    }
}

/**
 * 消息框键盘抬起
 */
function keyup(event: KeyboardEvent) {
    // shift 是否取消按下
    if (event.keyCode === 16) {
        shiftDown = false;
    }
}

// 消息框高度
const textareaHeight = ref(24);

// 监听消息框内容变化改变高度
watch(
    () => message.value,
    () => {
        // 根据换行符计算高度
        const lines = message.value.split("\n").length;
        textareaHeight.value = lines * 24;
    }
);

// 消息列表
const cacheClients = window.localStorage.getItem("message-data");
const clients: Array<IClient> = reactive(
    cacheClients ? JSON.parse(cacheClients) : []
);
const clientsIndex = ref(-1);

// 会话索引切换
watch(
    () => clientsIndex.value,
    async () => {
        if (clients[clientsIndex.value]) {
            document.title = clients[clientsIndex.value].name + " | ChatGPT";
            hljsInit();
            scrollToBottom();
            await nextTick();
            viewer.update();
            // MathJax.TypeSet();
        }
    }
);

/**
 * 创建新会话
 */
function newClient() {
    saveMessage();
    clients.unshift({
        name: `新会话`,
        contents: []
    });
    message.value = "";
    clientsIndex.value = 0;
}

/**
 * 删除会话
 */
function removeClient(i: number) {
    // 删除会话
    clients.splice(i, 1);
    clientsIndex.value = -1;
    saveMessage();
}

/**
 * 代码高亮
 */
async function hljsInit() {
    await nextTick();
    const dom: any = [];
    document.querySelectorAll("#messages pre code").forEach((el1: Element) => {
        const el = el1 as HTMLElement;
        if (!el.className.includes("hljs")) {
            const copyBtn = document.createElement("div");
            copyBtn.className = "copyBtn";
            copyBtn.innerHTML = "复制";
            copyBtn.setAttribute("code", el.innerText);
            copyBtn.addEventListener("click", (e) => {
                copyCode(e);
            });
            el.parentElement?.appendChild(copyBtn);
            hljs.highlightElement(el);
            dom.push(el);
        }
    });
    addCodeNum(dom);
}

/**
 * 添加代码行号
 */
function addCodeNum(dom: any) {
    for (let i = 0; i < dom.length; i++) {
        const enter = dom[i].innerHTML.replace(/\n/g, "</li><li>");
        dom[i].innerHTML = `<ol><li>${enter}</li></ol>`.replace(
            "<li></li></ol>",
            "</ol>"
        );
    }
}

// 侧边栏显示
const sideBarShow = ref(false);

/**
 * 复制代码块
 */
function copyCode(el: MouseEvent) {
    const code = (el.target as HTMLElement).getAttribute("code");
    if (code) {
        Clipboard.copy(code);
        messageUtil({
            type: "success",
            content: "复制成功"
        });
    }
}

/**
 * 重置配置
 */
function reloadConfig() {
    window.localStorage.removeItem("message-data");
    window.localStorage.removeItem("n0ts-chatgpt-theme");
    window.localStorage.removeItem("n0ts-chatgpt-config");
    window.location.reload();
}

// 设置显示
const settingShow = ref(false);
/**
 * 确认设定
 */
function toggleSetting() {
    settingShow.value = !settingShow.value;
    save();
    messageUtil({
        type: "success",
        content: "设置已保存"
    });
}

